<template>
  <div class="wrap wide">
    <div class="banner wide">
      <img class="wide" src="@/assets/images/riskArea.png" alt="">
      <router-link class="toRiskArea" to="/riskArea">
        全国中高风险疫情地区
        <i class="iconfont icon-xiayiyeqianjinchakangengduo"></i>
      </router-link>
      <p class="lv_banner_time">本服务由国务院客户端和各地卫健委提供相关数据</p>
    </div>
    <div class="covid-center">
      <div class="lv_search">
        <div class="lv_search_info" @click="cityQuery">
          <div class="iconfont icon-jiantou_zuoyouqiehuan">切换</div>
          <div class="">{{defaultArr[0]}}</div>
          <div class="">{{defaultArr[1]}}</div>
          <div class="iconfont icon-xiala2"></div>
        </div>
      </div>
      <div class="lv_search_list">
        <div class="lv_search_list_row" v-for="(item ,index) in risklevelList" :key="index">
          <p>{{item.areaName}}</p>
          <span>{{item.dangerLevel === 1? '低风险':(item.dangerLevel===2?'中风险':(item.dangerLevel===3?'高风险':''))}}</span>
        </div>
      </div>
    </div>

    <van-popup round v-model="show" position="bottom">
      <van-picker toolbar-position="top" show-toolbar @cancel="show = false" title="切换城市" :columns="columns"
        @confirm="onConfirm" />
    </van-popup>

    <div class="greenCode" v-if="lock">
      <img src="@/assets/images/risk_default.png" alt="">
      <p>常态化防控区域</p>
    </div>
  </div>
</template>

<script>
import { levelLinkage,situationToday, latestCovid, newsChannel, threeLevelLinkage,riskcity } from '@/api/indexCovid';
export default {
  data() {
    return {
      show: false,
      columns: [],
      risklevelList: [],
      lock:false,
      defaultArr: ['北京市', '朝阳区']
    };
  },
  mounted() {
    this.covidToday(this.defaultArr);
    this.citythree();
  },
  methods: {
    async covidToday(val) {
      let covidTodayData = await situationToday();
      let { todayDetailList } = covidTodayData.showapi_res_body;
      let singleProvince = todayDetailList.filter(item =>val[0] === item.provinceName);
      let multiple = singleProvince[0].dangerAreas.filter(item =>val[1] === item.cityName);
      this.risklevelList = multiple;
      multiple.length === 0 ? this.lock = true :this.lock = false
      
    },
    async citythree() {
      let cityData = await threeLevelLinkage();
      this.columns = cityData;
    },
    cityQuery() {
      this.show = true
    },
    onConfirm(values) {
      this.show = false;
      this.defaultArr = values;
      this.covidToday(values)
    }
  },
};
</script>
<style lang="less" scoped>
.wide {
  width: 100%;
  background-color: #FFF;
}

.wrap {
  position: relative;
  height: 1334px;

  .banner {
    position: relative;

    .toRiskArea {
      position: absolute;
      background-color: rgba(0, 0, 0, .2);
      top: 20px;
      right: 20px;
      border-radius: 100px;
      padding: 10px 12px;
      color: #FFF;
      font-size: 24px;
    }

    .lv_banner_time {
      position: absolute;
      font-size: 24px;
      color: #fff;
      bottom: 78px;
      left: 44px;
      right: 30px;
    }
  }

  .covid-center {
    .lv_search {
      position: relative;
      padding: 0 30px;

      .lv_search_info {
        display: flex;
        justify-content: space-around;
        align-items: center;
        height: 120px;
        background: #f8f8f8;
        border-radius: 36px;
        box-shadow: 0 0 30px rgba(0, 0, 0, .17);
        margin-top: -40px;
        font-size: 30px;
      }
    }

    .lv_search_list {
      padding: 0 30px;
      box-sizing: border-box;

      .lv_search_list_row {
        display: flex;
        justify-content: space-between;
        width: 100%;
        height: 100px;
        align-items: center;
        border-radius: 10px;
        background-color: #f8f8f8;
        font-size: 30px;
        margin-top: 30px;
        padding: 0 40px;

        p {
          flex: 1;
          text-overflow: -o-ellipsis-lastline;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          line-clamp: 1;
          -webkit-box-orient: vertical;
        }

        span {
          color: #fabe33;
          font-weight: bold;

        }
      }
    }
  }

  .van-picker {
    .van-picker__toolbar {

      .van-picker__cancel,
      .van-picker__confirm {
        font-size: 30px !important;
      }
    }
  }

  .greenCode {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;

    img {
      width: 240px;
      height: 240px;
      margin-bottom: 10px;
    }

    p {
      font-size: 46px;
      color: #65ca00;
      text-align: center;
      font-weight: 600;
    }
  }

}
</style>